/*start style elemnts*/
body {
	font-family: Rubik, sans-serif;
	background: #4ac29a; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#bdfff3,
		#4ac29a
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#bdfff3,
		#4ac29a
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

button:disabled {
	cursor: not-allowed;
}

footer {
	color: #f1e9e5;
}
/*end*/

/*start custom Clssess */
.main-title {
	color: #368b85;
}

.fit-content {
	width: fit-content;
}

.pointer-cursor {
	cursor: pointer;
}
/*end*/

/*start style page scroll */
/* https://css-tricks.com/the-current-state-of-styling-scrollbars/ */
html {
	--scrollbarBG: linear-gradient(90deg, #434343, #434343 1px, #111 0, #111);
	--thumbBG: #434343;
}

body::-webkit-scrollbar {
	width: 13px;
	height: 16px;
}

body {
	scrollbar-width: thin;
	scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}

body::-webkit-scrollbar-track {
	background: var(--scrollbarBG);
}

body::-webkit-scrollbar-thumb {
	background-color: var(--thumbBG);
	border-radius: 16px;
	border: 3px solid var(--scrollbarBG);
	box-shadow: inset 2px 2px 2px rgba(255, 255, 255, 0.25),
		inset -2px -2px 2px rgba(0, 0, 0, 0.25);
}
/*end*/

/*start Slider  */
.slider {
	height: 65vh;
	overflow-x: hidden;
	/* cursor: pointer; */
}
.slider__item {
	height: 100%;
	background-color: #eee;
	transition: all 0.3s ease-in-out;
}

.slider__item__image {
	height: 100%;
	width: 100%;
	cursor: grab;

	object-fit: cover;
}

.slider__item__info {
	bottom: 12%;
}

.slider__arrow {
	display: inline-block;
	padding: 5px 8px;
	background-color: #fff;
}

.slider__arrow-left {
	left: 20px;
}

.slider__arrow-right {
	right: 20px;
}

.slider__nav {
	bottom: 10px;
	/* background-color: yellow; */
}

.slider__nav__dot {
	cursor: pointer;
	width: 15px;
	height: 15px;
	background-color: white;
	margin-left: 12px;
}

.slider__nav__dot.active,
.slider__nav__dot:hover {
	background-color: red;
	border: 2px solid yellow;
}

.slider__nav__dot:first-of-type {
	margin-left: 0px;
}

/*end*/
